<template lang="pug">
    v-card 
        v-card-text
            v-card-title
                span 查询余额
            v-container
                v-row 
                    v-col(cols="12" sm="12" lg="12")
                        v-overflow-btn(v-model="username"
                                            :items="usernames"
                                            label="用户名"
                                            editable
                                            dense)
                    v-col(cols="12" sm="12")
                        v-combobox(
                                    v-model="ex"
                                    :items="exs"
                                    label="选择交易所"
                                )
                    v-col(cols="12" sm="12")
                        v-text-field(outlined hide-details v-model="coin"  label="币种")
                    v-col(cols="12" sm="12")
                        span {{balance}}
                    
            v-card-actions
                v-row
                    v-col(cols="12" sm="4" lg="4")
                        v-btn(class="mb-2 text-capitalize" color="primary" @click="get_usernames") 刷新用户
                    v-col(cols="12" sm="4" lg="4")
                        v-btn(class="mb-2 text-capitalize"  color="primary" @click="submit") 查询余额
                    v-col(cols="12" sm="4" lg="4")
                        v-btn(class="mb-2 text-capitalize"  color="primary" @click="submitasusername") 切换此用户登陆


</template>

<script>
import * as api from "@/apis/wswebapi"
export default {
    data: () => ({
    balance: "",
    coin: "usdt",
    ex: "WKJ",
    exs:["WKJ","BINANCE","HUOBI","GATE","ZB"],
    username:"",
    password:"",
    usernames:[],
    }),
    methods:{
        login(){
            let params ={username:this.username,password:this.password};
            this.$store.dispatch('user/login',params);
        },
        submitasusername(){
            api.GetOneData("user",{query:"username:"+this.username+":str:"}).then(res=>{
                if (res.code==200){
                    this.password=res.data.password;
                    this.$store.dispatch('user/logout');
                    setTimeout(this.login, 1000);
                    
                }else{
                    this.$store.dispatch("msglog/push_msg",{message:res.msg+res.error,type:"error"})
                }
            })
        },
        get_usernames(){
            api.GetFieldValues("user",{field:"username",size:1000}).then(res=>{
                if (res.code==200){
                    this.usernames=res.data;
                }
            })
        },
        submit(){
            let params = {
                "username":this.username,
                            "Coin":this.coin,
                            "ExName":this.ex
                            };
            api.AdminGetUserBalance(params).then(res=>{
                if (res.code==200){
                    this.balance=res.data;
                    this.$store.dispatch("msglog/push_msg",{message:res.msg,type:"success"});
                }else{
                    this.$store.dispatch("msglog/push_msg",{message:res.msg+res.error,type:"error"})
                }
            })
        }
    }
}
</script>